// app/NavBar.jsx
import Link from "next/link"; // Assure-toi que tu utilises Link de Next.js pour la navigation

export default function NavBar() {
 return (
  <nav className="bg-blue-500 p-4 shadow-lg">
   <ul className="flex justify-center space-x-8">
    <li>
     <Link
      href="/"
      className="text-white text-lg font-semibold hover:scale-110 transform transition duration-300"
     >
      Accueil
     </Link>
    </li>
    <li>
     <Link
      href="/catalogue"
      className="text-white text-lg font-semibold hover:scale-110 transform transition duration-300"
     >
      Catalogue
     </Link>
    </li>
    <li>
     <Link
      href="/session"
      className="text-white text-lg font-semibold hover:scale-110 transform transition duration-300"
     >
      Session
     </Link>
    </li>
    <li>
     <Link
      href="/contact"
      className="text-white text-lg font-semibold hover:scale-110 transform transition duration-300"
     >
      Contact
     </Link>
    </li>
   </ul>
  </nav>
 );
}
