"use client"; // Indique que ce composant utilise des fonctionnalités côté client, nécessaire pour `useState`

import { Inter } from "next/font/google";
import Header from "@/components/Header"; // Import de Header
import Footer from "@/components/Footer"; // Import de Footer
import "./globals.css"; // Import des styles globaux
import { useState } from "react";

// Import des pages que tu veux afficher
import Home from "./page"; // Page d'accueil
import Catalogue from "./catalogue/page"; // Page Catalogue
import Session from "./session/page"; // Page Session
import Contact from "./contact/page"; // Page Contact

const inter = Inter({ subsets: ["latin"] });

export default function RootLayout() {
 const [page, setPage] = useState("home");

 const renderPage = () => {
  switch (page) {
   case "home":
    return <Home />;
   case "catalogue":
    return <Catalogue />;
   case "session":
    return <Session />;
   case "contact":
    return <Contact />;
   default:
    return <Home />;
  }
 };

 return (
  <html lang="fr">
   <head>
    <meta charSet="UTF-8" />
    <meta name="viewport" content="width=device-width, initial-scale=1" />
    <title>Bibliothèque en ligne</title>
   </head>
   <body className={inter.className}>
    <Header changePage={setPage} currentPage={page} />{" "}
    {/* Header avec changement de page */}
    <main className="min-h-screen pt-16">
     {renderPage()} {/* Affichage de la page en fonction de l'état */}
    </main>
    <Footer changePage={setPage} /> {/* Footer avec changement de page */}
   </body>
  </html>
 );
}
